import _ from 'lodash'

import Notif from '../../mixins/notif'

import DashboardBox from '../../components/dashboard-box'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Notif
  ],

  components: {
    DashboardBox,
  },

  data() {
    return {
      data: null,
      isLoading: true,
      route,
    }
  },

  computed: {
    dash() {
      let moneyOpt = { style: 'currency', currency: 'PHP' }
      let moneyFormat = new Intl.NumberFormat('en-US', moneyOpt)

      return this.data ? {
        total: moneyFormat.format(this.data.totalWinningShares),
        previous: moneyFormat.format(this.data.previousWeek),
        current: moneyFormat.format(this.data.currentWeek)
      } : {
        total: 0,
        previous: 0,
        current: 0
      }
    },
  },

  mounted() {
    this.fetch()
  },

  methods: {
    async fetch() {
      try {
        this.data = await this.$store.dispatch('winning_shares/list')
      } catch(e) {
        this.handleError(e)
      }

      this.isLoading = false
    }
  }
}



